.Alumno {
  font-size: 2rem;
  height: 1rem;
  width: 80%;
  max-width: 40rem;
  margin: 0.5rem 4rem 0.5rem 4rem;
  padding: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: left;
  background-color: #3d0041;
  border-radius: 0.5rem;
  transition: all 2s linear;
  position: relative;
}
.Imagen {
  display: none;
}

.Datos {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 1rem;
}
.Datos * {
  width: 100%;
}
.Alumno > .Imagen {
  width: 10rem;
  height: 10rem;
  background-size: cover;
  text-align: center;

  position: relative;
  border-radius: 20%;
  background-color: #330036;
  border-radius: 20%;
}
.DatosAdicionales {
  display: none;
}
.Alumno.activo > .Datos > .DatosAdicionales {
  display: flex;
}
.Alumno.activo {
  height: min-content;
  transition: all 2s ease-in-out;
}
.Alumno.activo > .boton {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 1.5rem;
  margin-right: 1rem;
}
.boton {
  border: none;
  cursor: pointer;
  background-color: #330036;
  width: 3rem;
  height: 3rem;
  border-radius: 1.5rem;
  display: flex;
  place-content: center;
  align-items: center;
}
.boton > .linea {
  display: flex;
  background-color: #ffffff;
  width: 1.5rem;
  height: 0.5rem;
  border-radius: 0.5rem;
}
.boton:hover {
  outline: 1px solid #ffffff;
}
.Alumno.activo > .Imagen {
  display: flex;
  background-color: #330036;
}

.Alumno.activo > .Datos {
  width: 50%;
  flex-direction: column;
  align-items: flex-start;
}
.dato {
  word-wrap: break-word;
}
.Alumno > .Imagen.cargando {
  background-color: #3d0041;
  overflow: hidden;
}
.Alumno > .Imagen.cargando::after {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #330036;
  border-radius: 20%;
  transform: translate(-100%);
  animation: cargando 1s linear infinite;
}

.Alumno > .Imagen.cargado {
  background-image: url("../public/alumno.png");
}

@keyframes cargando {
  0% {
    background-color: #3d0041;
    transform: translate(-150, -150);
  }
  100% {
    transform: translate(150%);
    background-color: #330036;
  }
}
